<template>

	<view class="container">

		<view class="border_bottom" style="	justify-content: space-between; align-items: center;padding: 0 10upx;  height: 70upx;">
			<view class="icontext" style="width: 60upx ; padding-left: 20upx;" @click="erweima">&#xe611;</view>
			<view class="" style="width: 550upx; background-color: #DDDDDD; height: 44upx;  border-radius: 11upx;padding-left: 10upx;">
				<text class="icontext" style="font-size: 30upx;padding: 5upx ;">&#xe60f;</text>
				<input type="text" class="" style="height: 20upx; font-size:24upx;position: relative;top: -5upx;" v-model="search_title"
				 placeholder=" 齿轮城市" />
			</view>
			<view class="" style="width: 100upx ;font-size:30upx ; padding-left: 20upx;" @click="search">搜索</view>
		</view>





		<scroll-view class="jichukuangjia" scroll-y :scroll-top="scrollTop" @scroll="scroll" :style="'height:'+height+'px'">
			<view class="hot_card" v-for="(item,index) in luyouData_deal" :key="index">
				<image class="hot_card_img" :src="pic_before_title_loc+item.pic_url" @click="goDetail(item.card_id)"></image>
				<view class="hot_card_num_view">
					<text class="hot_card_num">{{item.time}}</text>
				</view>

				<view class="" style="font-size: 25upx; width: 95%; line-height: 60upx;height: 60upx; padding: 0upx  10upx; justify-content: space-between;">
					<view>{{item.title}}</view>
					<view v-if="!item.COLLECTION" class="icontext" @click="tap_collection(item,index)"> &#xe696;</view>
					<view v-if="item.COLLECTION" style="color: red;" class="icontext" @click="tap_collection(item,index)">
						&#xe646;</view>
					<view style="icontext" @click="share(item)" class="icontext">&#xe619;</view>
				</view>



			</view>

			<navigator style="width: 750upx; padding-left: 300upx;" url="../main_mulu/main_mulu" open-type="switchTab">返回</navigator>

		</scroll-view>
	</view>
</template>

<script>
	// import luyouData from '../common/luyou.js';
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				search_title: "",
				height: null,
				// pic_before_title: "http://kaikaiomg-kaikaiomg.stor.sinaapp.com/uni_app_image/card_pic/",
				luyouData: null,
				luyouData_deal: [],
				collect_list: [{}],
                  pic_before_title_loc:'',
			}
		},

		computed: {
			...mapState(['hasLogin', 'userInfo', 'phonenum', 'openid'])

		},

		onLoad: async function(option) {
			 this.pic_before_title_loc=this.pic_before_title;
			this.height = uni.getSystemInfoSync().windowHeight - 70 * (uni.getSystemInfoSync().windowWidth / 750);
			this.search_title = option.search_title;
			console.log('search_title', this.search_title)
			await this.get_hot_card_list()
			
			var post_data = {
				'form': 'get_collect_list',
				'openid': this.openid,
			};
			await this.download_from_server(post_data,'collect_list')
			this.deal_with_date();
			var post_data1= {
				'form': 'user_sousuo',
				'nickName': this.userInfo.nickName,
				'phonenum': this.phonenum,
				'openid': this.openid,
				'search_title': this.search_title,
			};
			this.upload_to_server(post_data1);
		},
		// 		onPullDownRefresh() {
		// 			console.log("下拉刷新");
		// 			this.refreshing = true;
		// 			this.getData();
		// 		},
		// 		onReachBottom() {
		// 			this.getData();
		// 		},
		methods: {
			tap_collection(item, index) {
				// 				console.log(this.categoryList)
				// 				console.log(this.subCategoryList)

				if (this.luyouData_deal[index].COLLECTION) {
					this.upload_to_server({
						'form': 'card_collection',
						'card_id': this.luyouData_deal[index].card_id,
						'nickName': this.userInfo.nickName,
						'phonenum': this.phonenum,
						'openid': this.openid,
						'avatarUrl': this.userInfo.avatarUrl,
						'collection_flag': 'false',
					});
					
					this.luyouData_deal[index].COLLECTION = false;
					uni.showToast({
						title: "取消关注",
						duration: 600,
					})
				} else {
					this.upload_to_server({
						'form': 'card_collection',
						'card_id': this.luyouData_deal[index].card_id,
						'nickName': this.userInfo.nickName,
						'phonenum': this.phonenum,
						'openid': this.openid,
						'avatarUrl': this.userInfo.avatarUrl,
						'collection_flag': 'true',
					});
					this.luyouData_deal[index].COLLECTION = true;
					uni.showToast({
						title: "关注成功",
						duration: 600,
					})

				}
				this.$forceUpdate()
				// this.subCategoryList[index]['COLLECTION']=true;
			},
			deal_with_date: function() {
				var that = this;
				if ((that.search_title.replace(/^\s\s*/, '').replace(/\s\s*$/, '')) == "") {
					return;
				}
				for (let i = 0; i < that.luyouData.length; i++) {
					if (that.luyouData[i].title.indexOf(that.search_title) != -1) {
						that.luyouData_deal.push(that.luyouData[i]);
					}

				}
				for (var j = 0; j < that.luyouData_deal.length; j++) {
					for (let k = 0; k < that.collect_list.length; k++) {
						if (that.luyouData_deal[j].card_id == that.collect_list[k].card_id && that.collect_list[k].collection_flag ==
							'true') {
							that.luyouData_deal[j].COLLECTION = true
							break;
						}
						if (that.luyouData_deal[j].card_id == that.collect_list[k].card_id && that.collect_list[k].collection_flag ==
							'false') {
							that.luyouData_deal[j].COLLECTION = false
							break;
						}
					}
				}





			},
		
			
			search: function() {
				// console.log("as1", this.hot_card_lists[0].title)
				uni.navigateTo({
					url: "../main_hot/hot?search_title=" + this.search_title,
				})
			},
		
			goDetail(card_id) {
				var url = "";
				var list_id = null
				for (var i = 0; i < this.luyouData.length; i++)
					if (this.luyouData[i].card_id == card_id) {
						url = this.luyouData[i].url;
						list_id = i;
					}
				uni.reLaunch({
					url: url + "?card_id=" + card_id + "&backpage=hot&list_id=" + list_id,

				})
			},
			


		}
	}
</script>

<style>
	.container {
		display: flex;
		flex-direction: column;

	}

	.jichukuangjia {
		background-color: #EFEFEF;
		width: 750upx;
		overflow: hidden;
		flex-flow: column nowrap;
		justify-content: center;
		align-items: center;
	}


	.hot_card {
		position: relative;
		margin: 30upx 75upx 30upx 75upx;
		border-radius: 30px;
		overflow: hidden;
		flex-direction: column;
		background-color: #FFFFFF;
		flex-flow: column;
		width: 600upx;
		height: 300upx;
	}

	.hot_card_img {

		width: 100%;
		height: 240upx;
	}

	.hot_card_bottm {
		font-size: 25upx;
		width: 90%;
		height: 60upx;
		justify-content: space-between;
		align-items: center;

	}



	.hot_card_num {
		color: #FFFFFF;
		font-size: 26px;
		text-align: center;
	}

	.hot_card_num_view {
		background-color: #FF80AB;
		height: 30px;
		line-height: 30px;
		position: absolute;
		border-radius: 30px;
		top: 20px;
		right: 20px;
	}

	::-webkit-scrollbar {
		width: 0;
		height: 0;
		color: transparent;
	}
</style>



